/* Btn
==================== */
.btn {
	
	display: inline-block;
	vertical-align: top;

	border-radius: 2px;
	border: 0;
	cursor: pointer;

	color: #fff;
	font-family: 'Roboto Medium', sans-serif;
	font-size: 16px;
	font-weight: 500;
	line-height: 18px;

	&--red {
		padding: 20px 55px;

		background-color: #fb565a;
	}

	&--work {
		padding: 16px 45px;

		background-color: #fb565a;
	}

	&--contacts {
		padding: 20px 45px;

		background-color: #fb565a;
	}
}



/* Header
==================== */
.header {

	width: 100%;
	height: 130px;

	position: absolute;
	top: 0;
	left: 0;
	z-index: 9999;
	
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 45px 0 20px 0;
	}

	&__basket {

		&-link {
			margin-left: 20px;

			color: #000;
			font-family: 'Roboto Medium', sans-serif;
			font-size: 16px;
			font-weight: 500;
			line-height: 30px;
			text-transform: uppercase;
		}		

		&-icon {
			width: 15px;
			height: 15px;
			fill: #a6a6a6;
		}
	}
}



/* Nav
==================== */
.nav {

	display: flex;
	align-items: center;
	
	&__link {
		margin-left: 50px;

		color: #000;
		font-family: 'Roboto Medium', sans-serif;
		font-size: 16px;
		font-weight: 500;
		line-height: 30px;
		text-transform: uppercase;

		&:first-child {
			margin-left: 0;
		}
	}
}



/* Intro
==================== */
.intro {

	display: flex;
	flex-wrap: wrap;

	height: 545px;
	padding-top: 130px;

	background-color: #efeeef;

	&__inner {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin-top: 50px;
	}

	&__header {
		width: 40%;
	}

	&__title {
		margin-bottom: 30px;

		color: #000;
		font-family: 'Roboto Medium', sans-serif;
		font-size: 55px;
		font-weight: 500;
		line-height: 55px;
	}

	&__subtitle {
		margin-bottom: 40px;

		color: #283136;
		font-family: 'Roboto Regular', sans-serif;
		font-size: 16px;
		font-weight: 400;
		line-height: 24px;
	}

	&__bg {
		position: relative;
		width: 60%;
	}

	&__img {
		position: absolute;
		top: -48px;
		left: -25px;

		height: auto;
	}
}



/* Work
==================== */
.work {

	&__inner {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		padding: 80px 0;

		border-bottom: 2px solid #eee;
	}

	&__item {
		width: 33.33333%;
	}

	&__img {
		max-width: 100%;

		margin-bottom: 25px;
	}

	&__title {
		margin-bottom: 25px;

		color: #000;
		font-family: 'Roboto Bold', sans-serif;
		font-size: 24px;
		font-weight: 700;
		line-height: 30px;
		text-transform: uppercase;
	}

	&__subtitle {
		margin-bottom: 35px;

		color: #283136;
		font-family: 'Roboto Regular', sans-serif;
		font-size: 16px;
		font-weight: 400;
		line-height: 24px;
	}
}



/* About
==================== */
.about {

	&__inner {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding: 80px 0;

		border-bottom: 2px solid #eee;
	}

	&__header {
		width: 60%;
	}


	&__title {
		margin-bottom: 35px;

		color: #000;
		font-family: 'Roboto Medium', sans-serif;
		font-size: 45px;
		font-weight: 500;
		line-height: 45px;
	}

	&__subtitle {
		margin-bottom: 48px;

		color: #283136;
		font-family: 'Roboto Regular', sans-serif;
		font-size: 16px;
		font-weight: 400;
		line-height: 24px;
	}

	&__order {
		margin-bottom: 35px;

		color: #000;
		font-family: 'Roboto Bold', sans-serif;
		font-size: 16px;
		font-weight: 700;
		text-transform: uppercase;	
	}

	&__item li {
		display: block;
		margin-bottom: 35px;

		color: #283136;
		font-family: 'Roboto Regular', sans-serif;
		font-size: 16px;
		font-weight: 400;

		&::before {
			content: "â€”";
			padding-right: 7px;
			color: #f00;
		}
	}

	&__statistics {
		position: relative;
		top: -40px;

		&-img {
			margin-bottom: 40px;
		}

		&-title {
			margin-bottom: 50px;

			color: #000;
			font-family: 'Roboto Bold', sans-serif;
			font-size: 16px;
			font-weight: 700;
			line-height: 24px;
			text-transform: uppercase;
			text-align: center;
		}

		&-percent {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		&-item {

			span {
				color: #000;
				font-family: 'Roboto Bold', sans-serif;
				font-size: 45px;
				font-weight: 700;
			}

			p {
				margin: 25px 0 0 0;

				color: #283136;
				font-family: 'Roboto Regular', sans-serif;
				font-size: 16px;
				font-weight: 400;
			}
		}
	}
}



/* Partners
==================== */
.partners {

	&__inner {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		padding: 45px 0;

		border-bottom: 2px solid #eee;
	}

	&__item {
		display: flex;
		justify-content: center;

		width: 25%;
		border-right: 2px solid #eee;

		&:last-child {
			border: none;
		}
	}
}



/* Contacts
==================== */
.contacts {

	background: #eee url("../img/maps/maps.jpg") center no-repeat;
	background-size: cover;

	&__inner {
		margin-top: 80px;
		padding: 55px 0;
	}

	&__block {
		display: inline-block;
		padding: 55px 50px 65px 50px;

		background-color: #fff;
	}

	&__title {
		margin-bottom: 30px;

		color: #000;
		font-family: 'Roboto Bold', sans-serif;
		font-size: 18px;
		font-weight: 700;
		line-height: 30px;
		text-transform: uppercase;
	}

	&__address {
		color: #666;
		font-family: 'Roboto Regular', sans-serif;
		font-style: normal;
		font-size: 16px;
		font-weight: 400;
		line-height: 18px;
	}

	&__city {
		margin-bottom: 20px;
	}

	&__phone {
		margin-bottom: 35px;
	}
}



/* Footer
==================== */
.footer {

	&__inner {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		margin: 80px 0;
	}

	&__title {
		margin-bottom: 10px;

		color: #000;
		font-family: 'Roboto Bold', sans-serif;
		font-size: 36px;
		font-weight: 700;
		line-height: 36px;
	}

	&__subtitle {
		color: #444;
		font-family: 'Roboto Regular', sans-serif;
		font-size: 16px;
		font-weight: 400;
		line-height: 22px;
	}
}



/* Social
==================== */
.social {

	display: flex;

	&__item {
		display: flex;
		justify-content: center;
		align-items: center;

		width: 80px;
		height: 80px;
		margin-right: 15px;

		background-color: #e1e1e1;
		border-radius: 50%;

		transition: background 0.1s linear;

		&:last-child {
			margin-right: 140px;
		}

		&:hover {
			background-color: #e74246;
		}
	}

	&__icon {
		display: block;
		height: 25px;

		fill: #fff;
	}
}